<template>
  <div id="app" class="font-sans text-gray-800">
    <header class="border-t-4 border-blue-100 bg-alex z-10 absolute w-full shadow-md fixed-top">
      <nav class="container mx-auto px-2 flex flex-wrap justify-between items-center py-1">
        <div class="mb-0 lg:mb-6 xl:mb-0 pt-0"> 
        </div>
				<div class="lg:hidden">
										
						<div class="languages">
             <language-switcher />
						</div>
         
				</div>
        <div class="block lg:hidden">
					
          <button @click="toggleMenu" class="flex items-center px-3 py-2 border rounded border-gray-500 hover:text-gray-600 hover:border-gray-600">
            <svg class="current-color h-3 w-3" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" fill="gray" /></svg>
          </button> 
					
        </div>
        <ul 
          class="uppercase tracking-wide font-bold w-full block flex-grow lg:flex lg:flex-initial lg:w-auto items-center mt-8 lg:mt-0"
          :class="false ? 'close': 'hidden'">
					
					<li class="mr-8 mb-6 lg:mb-0 color-alex-butn">
            <router-link :to="`/${$i18n.locale}/blog`" class="text-copy-primary hover:text-gray-600" v-on:click.native="scrollToPortFolio()" v-smooth-scroll>{{ $t('nav.portfolio') }}</router-link>
          </li>
          <li class="mr-8 mb-6 lg:mb-0 color-alex-butn">
            <router-link :to="`/${$i18n.locale}/blog`" class="text-copy-primary hover:text-gray-600" v-on:click.native="scrollToResume()" v-smooth-scroll>{{ $t('nav.resume') }}</router-link>
          </li>
          <li class="mr-8 mb-6 lg:mb-0 color-alex-butn">
            <router-link :to="`/${$i18n.locale}/blog`" class="text-copy-primary hover:text-gray-600" v-on:click.native="scrollToContact()" v-smooth-scroll>{{ $t('nav.contact') }}</router-link>
          </li>
          
          <li class="mr-8 mb-6 lg:mb-0 color-alex-butn">
            <router-link :to="`/${$i18n.locale}/tutorials`" class="text-copy-primary hover:text-gray-600" v-on:click.native="scrollToTop()">Tutorials</router-link>
          </li>
          
          <li class="mr-8 mb-6 lg:mb-0 color-alex-butn">
            <router-link :to="`/${$i18n.locale}/my_blog`" class="text-copy-primary hover:text-gray-600" v-on:click.native="scrollToTop()">Blog</router-link>
          </li>
          <li class="mb-4 lg:mb-0 lg:mr-8">
            <language-switcher />
          </li> 
        </ul>		
      </nav>
		
				<ul :class="menuOpen ? 'close': 'hidden'">
					
					<div id="semiland-mobile-drop-menu">
	
	
           <nav>
                  
             <ul class="menu">
		<li @click="toggleMenu"><router-link :to="`/${$i18n.locale}/blog`" class="text-copy-primary hover:text-gray-600" v-on:click.native="scrollToPortFolio()" v-smooth-scroll>{{ $t('nav.portfolio') }}</router-link></li> 
							
					</ul>
            <ul class="menu">
							<li @click="toggleMenu">
    <router-link :to="`/${$i18n.locale}/blog`" class="text-copy-primary hover:text-gray-600" v-on:click.native="scrollToResume()" v-smooth-scroll>{{ $t('nav.resume') }}</router-link>
  </li> 
							
					</ul> 
             <ul class="menu">
							<li @click="toggleMenu"><router-link :to="`/${$i18n.locale}/blog`" class="text-copy-primary hover:text-gray-600" v-on:click.native="scrollToContact()" v-smooth-scroll>{{ $t('nav.contact') }}</router-link></li> 
							
					</ul>
             
             <ul class="menu">
							<li @click="toggleMenu"><router-link :to="`/${$i18n.locale}/tutorials`" class="text-copy-primary hover:text-gray-600" v-on:click.native="scrollToTop()" v-smooth-scroll>TUTORIALS</router-link></li> 
							
					</ul>             
         </nav> 	
  </div> 
							
					
				</ul>
				
    </header>
    <div class="bg-gray-100 min-h-screen pt-20 text-lg">
      <router-view/>
    </div>
    
  </div>
</template>

<script>
	
import LanguageSwitcher from '@/components/LanguageSwitcher.vue'


export default {
  name: 'app',
  components: {
    LanguageSwitcher
  },
  data() {
    return {
      menuOpen: false, 
			menuOpen2: false
    }
  },
  methods: {
		scrollToResume() {
			var element = document.querySelector('#resume-id')
			var top = element.offsetTop
			
			var zero = 0
			window.scrollTo({ zero, top, behavior: 'smooth' });
    },
		scrollToPortFolio() {
			var element = document.querySelector('#portfolio-id')
			var top = element.offsetTop - 50 
			
			var zero = 0
			window.scrollTo({ zero, top, behavior: 'smooth' });
    },
		scrollToContact() {
			var element = document.querySelector('#contact-id')
			var top = element.offsetTop
			var zero = 0
			window.scrollTo({ zero, top, behavior: 'smooth' }); 
    },	
        scrollToTop() {
			window.scrollTo({ top:0, left:0 , behavior: 'smooth' });
    },
    toggleMenu() {
      this.menuOpen = !this.menuOpen
			document.querySelector('#drop-1').checked = false
			document.querySelector('#drop-2').checked = false
			document.querySelector('#drop-3').checked = false
    },
		check: function(e){
			if(e.target.id === 'drop-1'){
				document.querySelector('#drop-2').checked = false
				document.querySelector('#drop-3').checked = false
			}else if(e.target.id === 'drop-2'){
				document.querySelector('#drop-1').checked = false
				document.querySelector('#drop-3').checked = false
			}else{
				document.querySelector('#drop-1').checked = false
				document.querySelector('#drop-2').checked = false
			}
		}
  }
}
</script>
<style src="@/assets/css/tailwind.css" />

<link href="https://fonts.googleapis.com/css?family=Open+Sans&display=swap" rel="stylesheet">
 
